<template>
    <div class="p-button">
        <p :class="{'active': status, 'active-not': !status}" @click="confirm">{{name}}</p>
    </div>
</template>

<script>
    export default {
        name: 'p-button',
        props: {
            name: {
                type: String
            },
            status: {
                type: Boolean
            }
        },
        methods: {
            confirm () {
                this.$emit('handleClick');
            }
        }
    };
</script>

