<template>
    <div class="select-area bg">
        <div class="area-content">
            <div class="area-flex">
                <div class="flex-left">
                    <ul>
                        <li class="border-right" :class="{'left-bg': indexCountry == idx}" v-for="(country, idx) in countryList" @click="chooseCountry(country, idx)">{{country.name}}</li>
                    </ul>
                </div>
                <div class="flex-right">
                    <ul>
                        <li :class="{'chooseCity': indexCity == idx}" v-for="(city, idx) in cityList" @click="chooseCity(city, idx)">{{city.name}}</li>
                    </ul>
                </div>
            </div>
        </div>
        <p-button @handleClick="confirm" :name="$t('btn.confirm')" :status="btnActive"></p-button>
    </div>
</template>

<script>
    import pButton from '../../components/p-button.vue';
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';

    export default {
        name: 'select-area',
        components: { pButton },
        data () {
            return {
                countryList: [],
                cityList: [],
                indexCountry: 0,
                indexCity: 0,
                city: '',
                btnActive: true
            };
        },
        created () {
            this.$router.setTitle(this.$t('title.city'));
            this.getArea();
        },
        methods: {
            getArea () {
                let params = {},
                    self = this;
                publicPost(getApiUrl('getArea'), params).then((res) => {
                    self.countryList = res;
                    self.cityList = res[self.indexCountry].children;
                }).catch((e) => {
                    self.$toast.center(e);
                });
            },
            chooseCountry (item, idx) {
                if (this.indexCountry !== idx) {
                    this.indexCountry = idx;
                    this.indexCity = 0;
                    if (item.children.length) {
                        this.cityList = item.children;
                    } else {
                        this.cityList = [];
                    }
                }
            },
            chooseCity (item, idx) {
                this.city = item;
                this.indexCity = idx;
            },
            confirm () {
                if (this.countryList[this.indexCountry].children.length) {
                    this.$root.city = this.cityList[this.indexCity];
                } else {
                    this.$root.city = this.countryList[this.indexCountry];
                }
                this.$router.go(-1);
            }
        }
    };
</script>

