    @import "../../scss/variables.scss";

    .select-area {
        width: 100%;
        height: 100%;
        overflow: hidden;
        .area-content {
            width: 100%;
            height: 100%;
            overflow: scroll;
            .area-flex {
                display: flex;
                flex-direction: row;
                margin-bottom: pxtorem(70px);
                .flex-left {
                    width: pxtorem(125px);
                    background: #f6f6f6;
                }
                .flex-right {
                    flex: 1;
                }
                li {
                    padding: pxtorem(14px) 0 pxtorem(14px) pxtorem(16px);
                    font-size: pxtorem(16px);
                    color: #222;
                    border-bottom: 1px solid #e1e1e1;
                }
                .border-right {
                    border-right: 1px solid #e1e1e1;
                }
                .left-bg {
                    background: #fff;
                    border-right: none;
                }
                .chooseCity {
                    background: url("../../assets/publish/radio.png") pxtorem(220px) center no-repeat;
                    background-size: pxtorem(14px) pxtorem(14px);
                }
            }
        }
    }

</style>
